import React , {useState, useEffect} from 'react';
import Constantes from "../Constantes";
import { Modal, ModalBody, ModalHeader, ModalFooter } from 'reactstrap';
import { format } from 'date-fns';
//jQuery libraries
import 'jquery/dist/jquery.min.js';
//Datatable Modules
import "datatables.net-dt/js/dataTables.dataTables"
import "datatables.net-dt/css/jquery.dataTables.min.css"
import $ from 'jquery'; 


//AUTH0
import { useAuth0 } from "@auth0/auth0-react";

function Operations () {

    const { user  } = useAuth0();

    const [data, setData] = useState([]);
    const [options, setOptions] = useState([]);
    const [operation, setValues] = useState({ 
      id: '' , concept: '', amount: '', date: '', type: '' , category_id: '' , user_email: `${user.email}`
    });
    const [updated , setUpdated] = useState(false);
  
    useEffect(() => {
      (async () => {
        const result = await fetch(`${Constantes.API_ROUTE}/operations/user/${user.email}`, {
          method: 'GET',
          mode: 'cors',
          headers: {
            'Content-Type': 'application/json'
          }
        });
        const result_categories = await fetch(`${Constantes.API_ROUTE}/categories/user/${user.email}`, {
          method: 'GET',
          mode: 'cors',
          headers: {
            'Content-Type': 'application/json'
          }
        });
        let data = await result.json();
        let options = await result_categories.json();
        setData(data);
        setOptions(options);
        setUpdated(false)
      })();
    }, [updated]);
    
    const [modalAdd, setModalAdd] = useState(false);
    const [modalEdit, setModalEdit] = useState(false);
    const [modalDelete, setModalDelete] = useState(false);
  
    const select=(object, action)=>{
      switch (action) {
        case 'Add':
          setValues({
            id: '' , concept: '', amount: '', date: '', type: '' , category_id: '' , user_email: `${user.email}`
          });
          setModalAdd(true);
          break;
        case 'Edit':
          (async () => {
            const result = await fetch(`${Constantes.API_ROUTE}/operations/${object}`, {
              method: 'GET',
              mode: 'cors',
              headers: {
                'Content-Type': 'application/json'
              }
            });
            let data = await result.json();
            setValues(data[0]);
          })();
          setModalEdit(true);
          break;
        case 'Delete':
          if(object > 0){
            (async () => {
              const result = await fetch(`${Constantes.API_ROUTE}/operations/${object}`, {
                method: 'DELETE',
                mode: 'cors',
                headers: {
                  'Content-Type': 'application/json'
                }
              });
              await result.json();
              setModalDelete(false);
              setUpdated(true);
            })();
          }else{
            setValues(object)
            setModalDelete(true);
          }
          break;
        default:
          break;
      }
    };
  
    const handleChange=e=>{
      const {name, value}=e.target;
      setValues((prevState)=>({
        ...prevState,
        [name]: value
      }));
    }
  
    const saveFormData = async () => {
      let url = (operation.id !== '') ? `${Constantes.API_ROUTE}/operations/${operation.id}` : `${Constantes.API_ROUTE}/operations`
      const response = await fetch(url, {
        method: (operation.id !== '') ? 'PUT' : 'POST',
        mode: 'cors',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(operation)
      });
      if (response.status !== 200) {
        throw new Error(`Request failed: ${response.status}`); 
      }
      return response.json();
    }
  
    const onSubmit = async (event) => {
     event.preventDefault(); // Prevent default submission
      try {
        await saveFormData();
        if(modalAdd === true){
          setModalAdd(false)
        }else if (modalEdit === true){
          setModalEdit(false)
        }
        setUpdated(true);
        setValues({
          id: '' , concept: '', amount: '', date: '', type: '' , category_id: ''
        });
      } catch (e) {
        alert(`Registration failed! ${e.message}`);
      }
    };

    function category (category_id) {
      for (let i = 0; i < options.length; i++) {
        if(options[i].id === category_id){
            return options[i].name;
        }
      }
    }

    function formatCurrency (locales, currency, fractionDigits, number) {
      var formatted = new Intl.NumberFormat(locales, {
        style: 'currency',
        currency: currency,
        minimumFractionDigits: fractionDigits
      }).format(number);
      return formatted;
    }

    function CurrentBalance () {
      var CurrentBalance = 0; 
      for (let i = 0 ; i < data.length; i++) {
        if(data[i].type === 'Ingreso') {
            CurrentBalance+=data[i].amount;
        }else{
            CurrentBalance-=data[i].amount;
        }
      }
      return CurrentBalance;
    }   

    $(() => {
      setTimeout(function(){
        $('#dataTable').DataTable();
       } ,500);
    });
    
    return (
        <div className="p-3">
            <div className="float-start">
            <h2><i className='bi bi-piggy-bank'></i> Current Balance: {formatCurrency("es-CO" , "COP" , 2 , CurrentBalance())} </h2>
            </div>
            <div className="float-end">
                <button className="btn btn-success" onClick={()=>select(null,'Add')}>Add Operation</button>
            </div>
            <table id="dataTable" className="table table-bordered table-striped table-hover">
              <thead className="table-dark">
                  <tr>
                    <th>#</th>
                    <th>Concept</th>
                    <th>Amount</th>
                    <th>Date</th>
                    <th>Type</th>
                    <th>Category</th>
                    <th>Actions</th>
                  </tr>
              </thead>
              <tbody>
              {data.map((operation, index) => (
                  <tr key={operation.id}>
                      <th scope="row">{index+1}</th>
                      <td>{operation.concept}</td>
                      <td>{formatCurrency("es-CO", "COP", 2, operation.amount)}</td>
                      <td>{format(new Date(operation.date), 'yyyy-MM-dd')}</td>
                      <td>{operation.type}</td>
                      <td>{category(operation.category_id)}</td>
                      <td>
                          <button className="btn btn-primary" onClick={()=>select(operation.id, 'Edit')}><i className="bi bi-pencil-fill" title="Edit"></i></button>
                          <button className="btn btn-danger" onClick={()=>select(operation,'Delete')}><i className="bi bi-trash3-fill" title="Delete"></i></button>
                      </td>
                  </tr>
                ))
              }
              </tbody>
            </table>
            <Modal centered isOpen={modalAdd}>
                <ModalHeader toggle={()=>setModalAdd(false)}>
                Add Operation
                </ModalHeader>
                <ModalBody>
                    <div className="row">
                        <form onSubmit={onSubmit}>
                          <div className="col-md-12">
                              <div className="form-group">
                              <label>Concept:</label>
                              <input className="form-control" name="concept" type={'text'} value={operation.concept} onChange={handleChange} required/>
                              </div>
                          </div>
                          <div className="col-md-12">
                              <div className="form-group">
                              <label>Amout:</label>
                              <input className="form-control" name="amount" type={'number'} value={operation.amount} onChange={handleChange} required/>
                              </div>
                          </div>
                          <div className="col-md-12">
                              <div className="form-group">
                              <label>Date:</label>
                              <input className="form-control" name="date" type={'date'} value={operation.date} onChange={handleChange} required/>
                              </div>
                          </div>
                          <div className="col-md-12">
                              <div className="form-group">
                              <label>Type:</label>
                              <select className="form-select" name="type" value={operation.type} onChange={handleChange} required>
                                  <option>Choose an option</option>
                                  <option value="Ingreso">Income</option>
                                  <option value="Egreso">Expense</option>
                              </select>
                              </div>
                          </div>
                          <div className="col-md-12">
                              <div className="form-group">
                                <label>Categories:</label>
                                <select className="form-select" name="category_id" value={operation.category_id} onChange={handleChange} required>
                                  <option>Choose an option</option>
                                    {options.map((option) => (
                                      <option key={option.id} value={option.id}>{option.name}</option>
                                    ))}
                                </select>
                              </div>
                          </div>
                          <div className="col-md-12">
                              <div className="d-flex justify-content-center mt-3">
                              <button className="btn btn-primary" type='submit'>Send</button>
                              </div>
                          </div>
                        </form>
                    </div>
                </ModalBody>
                <ModalFooter>
                    <button className="btn btn-danger" onClick={()=>setModalAdd(false)}>Cancel</button>
                </ModalFooter>
            </Modal>

            <Modal isOpen={modalEdit}>
                <ModalHeader toggle={()=>setModalEdit(false)}>
                Edit Operation
                </ModalHeader>
                <ModalBody>
                <div className="row">
                    <form onSubmit={onSubmit}>
                    <div className="col-md-12">
                        <div className="form-group">
                        <label>Concept:</label>
                        <input className="form-control" name="concept" type={'text'} value={operation.concept} onChange={handleChange} required/>
                        </div>
                    </div>
                    <div className="col-md-12">
                        <div className="form-group">
                        <label>Amout:</label>
                        <input className="form-control" name="amount" type={'number'} value={operation.amount} onChange={handleChange} required/>
                        </div>
                    </div>
                    <div className="col-md-12">
                        <div className="form-group">
                        <label>Date:</label>
                        <input className="form-control" name="date" type={'date'} value={(operation.date !== '') ? format(new Date(operation.date), 'yyyy-MM-dd') : ''} onChange={handleChange} required/>
                        </div>
                    </div>
                    <div className="col-md-12">
                        <div className="form-group">
                        <label>Type:</label>
                        <input className="form-control" value={operation.type} readOnly/>
                        </div>
                    </div>
                    <div className="col-md-12">
                      <div className="form-group">
                        <label>Categories:</label>
                        <select className="form-select" name="category_id" value={operation.category_id} onChange={handleChange} required>
                          <option>Choose an option</option>
                            {options.map((option) => (
                              <option key={option.id} value={option.id}>{option.name}</option>
                            ))}
                        </select>
                      </div>
                    </div>
                    <div className="col-md-12">
                        <div className="d-flex justify-content-center mt-3">
                        <button className="btn btn-primary" type='submit'>Send</button>
                        </div>
                    </div>
                    </form>
                </div>
                </ModalBody>
                <ModalFooter>
                <button className="btn btn-danger" onClick={()=>setModalEdit(false)}>Cancel</button>
                </ModalFooter>
            </Modal>

            <Modal centered isOpen={modalDelete}>
                <ModalHeader toggle={()=>setModalDelete(false)}>
                Delete Operation
                </ModalHeader>
                <ModalBody>
                    Are you sure you want to permanently delete this operation?
                    Description:  {operation.concept} ; {operation.type}
                </ModalBody>
                <ModalFooter>
                <button className="btn btn-success" onClick={()=>select(operation.id, 'Delete')}>Confirm</button>
                <button className="btn btn-danger" onClick={()=>setModalDelete(false)}>Cancel</button>
                </ModalFooter>
            </Modal>
        </div>   
    );
}

export default Operations;