import React from 'react';
import { Routes, Route} from "react-router-dom";
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.bundle.js';
import 'bootstrap-icons/font/bootstrap-icons.css';
import NavBar from "./Components/NavBar";
import Operations from "./Components/Operations";
import Categories from "./Components/Categories";
import Welcome from "./Components/Welcome";
import Spinner from "./Components/Spinner";
import "./index.css";
import Constantes from "./Constantes";

//AUTH0
import { useAuth0 } from "@auth0/auth0-react";

export default function HomeApp() {
  const { isLoading , isAuthenticated , user  } = useAuth0();

  isAuthenticated && (
    (async () => {
      const result = await fetch(`${Constantes.API_ROUTE}/users/${user.email}`, {
        method: 'GET',
        mode: 'cors',
        headers: {
          'Content-Type': 'application/json'
        }
      });
      let data = await result.json();
      if(data.length === 0){
        await fetch(`${Constantes.API_ROUTE}/users`, {
          method: 'POST',
          mode: 'cors',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify(user)
        });
      }
    })()
  )
  
  return (
    <Routes>
      <Route path="/" element={(isLoading) ? <Spinner /> : ((isAuthenticated) ? <NavBar /> : <Welcome/>)}>
        <Route path="/" element={(isLoading) ? <Spinner /> : ((isAuthenticated) ? <Operations /> : <Welcome/>)} />
        <Route path="/categories" element={(isLoading) ? <Spinner /> : ((isAuthenticated) ? <Categories /> : <Welcome/>) } />
      </Route>
    </Routes>
  );
}