import React from 'react';
import logo from './../logo.svg';
import { Link , Outlet , useLocation } from "react-router-dom";
import { useAuth0 } from "@auth0/auth0-react";

function NavBar(){
    const { user , logout } = useAuth0();

    //assigning location variable
    const location = useLocation();

    //destructuring pathname from location
    const { pathname } = location;

    //Javascript split method to get the name of the path in array
    const splitLocation = pathname.split("/");

    return (
        <div className="App">
            <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
                <div className="container-fluid">
                    <div className="navbar-brand">
                        <div className="dropdown">
                            <a href="https://github.com/JefferGonzalez" className="d-flex align-items-center text-white text-decoration-none dropdown-toggle" data-bs-toggle="dropdown" aria-expanded="false">
                                <img alt={user.name} src={(user.picture !== null) ? user.picture : logo} style={{width:"auto" , height:"30px"}} className="rounded-circle me-2"/>
                                <strong className="fs-6">{user.name}</strong>
                            </a>
                            <ul className="dropdown-menu text-small shadow" aria-labelledby="dropdownUser">
                                <li><button className="dropdown-item" onClick={() => logout()}>Cerrar Sesión</button></li>
                            </ul>
                        </div>
                    </div>
                    <button className='navbar-toggler' type="button" data-bs-toggle="collapse" data-bs-target="#navbarText" aria-controls="navbarText" aria-expanded="false" aria-label="Toggle navigation">
                        <span className='navbar-toggler-icon'></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarText">
                    <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                        <li className="nav-item">
                            <Link className={splitLocation[1] === "" ? "nav-link active" : "nav-link"} aria-current="page" to="/">Home</Link>
                        </li>
                        <li className="nav-item">
                            <Link className={splitLocation[1] === "categories" ? "nav-link active" : "nav-link"} aria-current="page" to="/categories">Categories</Link>
                        </li>
                    </ul>
                    <span className="navbar-text fw-bolder">
                        ABM OF OPERATIONS
                        <img src={logo} alt="Xue" style={{width:"auto" , height:"30px"}} className="d-inline-block align-text-top"/>
                    </span>
                    </div>
                </div>
            </nav>
            <Outlet />
        </div>
    );
}
export default NavBar;