import React from 'react';
import { useAuth0 } from "@auth0/auth0-react";

function Welcome () {
    const { loginWithRedirect } = useAuth0();

    return (
    <div className="position-absolute top-50 start-50 translate-middle">
        <div className="h-100 text-center text-dark">
            <div className="cover-container d-flex w-100 h-100 p-3 mx-auto flex-column">
                <main className="px-3 h-100">
                <h1>Organize your money.</h1>
                <p className="lead">On our platform you can take control of your finances.</p>
                <p className="lead">
                    <button className="btn btn-lg btn-dark fw-bold border-dark bg-dark" onClick={() => loginWithRedirect()}>Log in</button>
                </p>
                </main>
            </div>
        </div>
    </div>
    );
}

export default Welcome;

