import React , {useState, useEffect} from 'react';
import Constantes from "../Constantes";
import { Modal, ModalBody, ModalHeader, ModalFooter } from 'reactstrap';
import { format } from 'date-fns';
//jQuery libraries
import 'jquery/dist/jquery.min.js';
//Datatable Modules
import "datatables.net-dt/js/dataTables.dataTables"
import "datatables.net-dt/css/jquery.dataTables.min.css"
import $ from 'jquery'; 

//AUTH0
import { useAuth0 } from "@auth0/auth0-react";

function Categories () {
    
    const { user  } = useAuth0();
    
    const [data, setData] = useState([]);
    const [category, setValues] = useState({ 
      id: '' , name: '', description: '', date_created: '' , user_email: `${user.email}`
    });
    const [updated , setUpdated] = useState(false);
  
    useEffect(() => {
      (async () => {
        const result = await fetch(`${Constantes.API_ROUTE}/categories/user/${user.email}`, {
          method: 'GET',
          mode: 'cors',
          headers: {
            'Content-Type': 'application/json'
          }
        });
        let data = await result.json()
        setData(data);
        setUpdated(false)
      })();
    }, [updated]);
    
    const [modalAdd, setModalAdd] = useState(false);
    const [modalEdit, setModalEdit] = useState(false);
    const [modalDelete, setModalDelete] = useState(false);
  
    const select=(object, action)=>{
      switch (action) {
        case 'Add':
          setValues({
            id: '' , name: '', description: '', date_created: '' , user_email: `${user.email}`
          });
          setModalAdd(true);
          break;
        case 'Edit':
          (async () => {
            const result = await fetch(`${Constantes.API_ROUTE}/categories/${object}`, {
              method: 'GET',
              mode: 'cors',
              headers: {
                'Content-Type': 'application/json'
              }
            });
            let data = await result.json();
            setValues(data[0]);
          })();
          setModalEdit(true);
          break;
        case 'Delete':
          if(object > 0){
            (async () => {
              const result = await fetch(`${Constantes.API_ROUTE}/categories/${object}`, {
                method: 'DELETE',
                mode: 'cors',
                headers: {
                  'Content-Type': 'application/json'
                }
              });
              await result.json();
              setModalDelete(false);
              setUpdated(true);
            })();
          }else{
            setValues(object)
            setModalDelete(true);
          }
          break;
        default:
          break;
      }
    };
  
    const handleChange=e=>{
      const {name, value}=e.target;
      setValues((prevState)=>({
        ...prevState,
        [name]: value
      }));
    }
  
    const saveFormData = async () => {
      let url = (category.id !== '') ? `${Constantes.API_ROUTE}/categories/${category.id}` : `${Constantes.API_ROUTE}/categories`
      const response = await fetch(url, {
        method: (category.id !== '') ? 'PUT' : 'POST',
        mode: 'cors',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(category)
      });
      if (response.status !== 200) {
        throw new Error(`Request failed: ${response.status}`); 
      }
      return response.json();
    }
  
    const onSubmit = async (event) => {
     event.preventDefault(); // Prevent default submission
      try {
        await saveFormData();
        if(modalAdd === true){
          setModalAdd(false)
        }else if (modalEdit === true){
          setModalEdit(false)
        }
        setUpdated(true);
        setValues({
            id: '' , name: '', description: '', date_created: ''
        });
      } catch (e) {
        alert(`Registration failed! ${e.message}`);
      }
    };

    $(() => {
      setTimeout(function(){
        $('#dataTable').DataTable();
       } ,500);
    });

    return (
        <div className="p-3">
            <div className="d-flex justify-content-center mb-3">
              <button className="btn btn-success" onClick={()=>select(null,'Add')}>Add Category</button>
            </div>
            <table id='dataTable' className="table table-bordered table-striped table-hover">
                <thead className="table-dark">
                    <tr>
                        <th>#</th>
                        <th>Name</th>
                        <th>Description</th>
                        <th>Date Created</th>
                        <th>Actions</th>
                    </tr>
                </thead>
                <tbody>
                {data.map((category, index) => (
                    <tr key={category.id}>
                        <th scope="row">{index+1}</th>
                        <td>{category.name}</td>
                        <td>{category.description}</td>
                        <td>{format(new Date(category.date_created), 'yyyy-MM-dd')}</td>
                        <td>
                            <button className="btn btn-primary" onClick={()=>select(category.id, 'Edit')}><i className="bi bi-pencil-fill" title="Edit"></i></button>
                            <button className="btn btn-danger" onClick={()=>select(category,'Delete')}><i className="bi bi-trash3-fill" title="Delete"></i></button>
                        </td>
                    </tr>
                ))}
                </tbody>
            </table>
            <Modal centered isOpen={modalAdd}>
                <ModalHeader toggle={()=>setModalAdd(false)}>
                Add Category
                </ModalHeader>
                <ModalBody>
                    <div className="row">
                        <form onSubmit={onSubmit}>
                            <div className="col-md-12">
                                <div className="form-group">
                                <label>Name:</label>
                                <input className="form-control" name="name" type={'text'} value={category.name} onChange={handleChange} required/>
                                </div>
                            </div>
                            <div className="col-md-12">
                                <div className="form-group">
                                <label>Description:</label>
                                <input className="form-control" name="description" type={'text'} value={category.description} onChange={handleChange} required/>
                                </div>
                            </div>
                            <div className="col-md-12">
                                <div className="d-flex justify-content-center mt-3">
                                <button className="btn btn-primary" type='submit'>Send</button>
                                </div>
                            </div>
                        </form>
                    </div>
                </ModalBody>
                <ModalFooter>
                    <button className="btn btn-danger" onClick={()=>setModalAdd(false)}>Cancel</button>
                </ModalFooter>
            </Modal>

            <Modal isOpen={modalEdit}>
                <ModalHeader toggle={()=>setModalEdit(false)}>
                Edit Category
                </ModalHeader>
                <ModalBody>
                <div className="row">
                    <form onSubmit={onSubmit}>
                        <div className="col-md-12">
                            <div className="form-group">
                            <label>Name:</label>
                            <input className="form-control" name="name" type={'text'} value={category.name} onChange={handleChange} required/>
                            </div>
                        </div>
                        <div className="col-md-12">
                            <div className="form-group">
                            <label>Description:</label>
                            <input className="form-control" name="description" type={'text'} value={category.description} onChange={handleChange} required/>
                            </div>
                        </div>
                        <div className="col-md-12">
                            <div className="form-group">
                            <label>Date Of Created:</label>
                            <input className="form-control" readOnly type={'text'} value={(category.date_created !== '') ? format(new Date(category.date_created), 'yyyy-MM-dd') : ''} onChange={handleChange}/>
                            </div>
                        </div>
                        <div className="col-md-12">
                            <div className="d-flex justify-content-center mt-3">
                            <button className="btn btn-primary" type='submit'>Send</button>
                            </div>
                        </div>
                    </form>
                </div>
                </ModalBody>
                <ModalFooter>
                <button className="btn btn-danger" onClick={()=>setModalEdit(false)}>Cancel</button>
                </ModalFooter>
            </Modal>

            <Modal centered isOpen={modalDelete}>
                <ModalHeader toggle={()=>setModalDelete(false)}>
                Delete Category
                </ModalHeader>
                <ModalBody>
                    Are you sure you want to permanently delete this category?
                    Info:  {category.name} ; {category.description}
                </ModalBody>
                <ModalFooter>
                <button className="btn btn-success" onClick={()=>select(category.id, 'Delete')}>Confirm</button>
                <button className="btn btn-danger" onClick={()=>setModalDelete(false)}>Cancel</button>
                </ModalFooter>
            </Modal>
        </div>
    );
}

export default Categories;